@font-face {
    font-family: Montserrat-Bold;
    src: url(../fonts/montserrat/Montserrat-Bold.ttf)
}

@font-face {
    font-family: Montserrat-SemiBold;
    src: url(../fonts/montserrat/Montserrat-SemiBold.ttf)
}

@font-face {
    font-family: Montserrat-Regular;
    src: url(../fonts/montserrat/Montserrat-Regular.ttf)
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box
}

body,
html {
    height: 100%;
    font-family: Montserrat-Regular, sans-serif
}

a {
    font-family: Montserrat-Regular;
    font-size: 14px;
    line-height: 1.7;
    color: #666;
    margin: 0;
    transition: all .4s;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    -moz-transition: all .4s
}

a:focus {
    outline: none !important
}

a:hover {
    text-decoration: none
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0
}

p {
    font-family: Montserrat-Regular;
    font-size: 14px;
    line-height: 1.7;
    color: #666;
    margin: 0
}

ul,
li {
    margin: 0;
    list-style-type: none
}

input {
    outline: none;
    border: none
}

textarea {
    outline: none;
    border: none
}

textarea:focus,
input:focus {
    border-color: transparent !important
}

input:focus::-webkit-input-placeholder {
    color: transparent
}

input:focus:-moz-placeholder {
    color: transparent
}

input:focus::-moz-placeholder {
    color: transparent
}

input:focus:-ms-input-placeholder {
    color: transparent
}

textarea:focus::-webkit-input-placeholder {
    color: transparent
}

textarea:focus:-moz-placeholder {
    color: transparent
}

textarea:focus::-moz-placeholder {
    color: transparent
}

textarea:focus:-ms-input-placeholder {
    color: transparent
}

input::-webkit-input-placeholder {
    color: #adadad
}

input:-moz-placeholder {
    color: #adadad
}

input::-moz-placeholder {
    color: #adadad
}

input:-ms-input-placeholder {
    color: #adadad
}

textarea::-webkit-input-placeholder {
    color: #adadad
}

textarea:-moz-placeholder {
    color: #adadad
}

textarea::-moz-placeholder {
    color: #adadad
}

textarea:-ms-input-placeholder {
    color: #adadad
}

label {
    display: block;
    margin: 0
}

button {
    outline: none !important;
    border: none;
    background: 0 0
}

button:hover {
    cursor: pointer
}

iframe {
    border: none !important
}

.bg-container-contact100 {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0
}

.bg-container-contact100::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -11;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(41, 44, 51, .6);
    pointer-events: none
}

.contact100-header {
    width: 100%;
    height: 80px;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    padding: 10px 35px
}

.contact100-header-logo img {
    max-height: 60px
}

.btn-show-contact100 {
    font-family: Montserrat-Bold;
    font-size: 12px;
    color: #846add;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: block;
    min-width: 160px;
    height: 42px;
    background-color: #fff;
    border-radius: 21px;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s
}

.btn-show-contact100:hover {
    color: #fff;
    background-color: #846add
}

.container-contact100 {
    /* width: 100%; */
    min-height: 100vh;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    /* padding: 15px; */
    padding: 0.5em;
    background: rgba(132, 106, 221, .8);
    position: relative;
    z-index: 10
}

.wrap-contact100 {
    width: 560px;
    background: #fff;
    border-radius: 3px;
    position: relative;
    margin-top: 40px
}

.contact100-form-title {
    font-family: Montserrat-Bold;
    font-size: 30px;
    color: #fff;
    line-height: 1.2;
    text-transform: uppercase;
    width: 100%;
    min-height: 128px;
    position: relative;
    z-index: 1;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    overflow: hidden;
    padding: 30px 15px
}

.contact100-form-title::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(67, 67, 67, .6)
}

.contact100-form {
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 40px 55px 48px
}

.wrap-input100 {
    width: 100%;
    position: relative;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
    margin-bottom: 15px
}

.label-input100 {
    font-size: 18px;
    color: #999;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 60px;
    height: 100%;
    top: 0;
    left: 2px
}

.label-input100.rs1 {
    height: 60px
}

.input100 {
    display: block;
    width: 100%;
    background: 0 0;
    font-family: Montserrat-Regular;
    font-size: 15px;
    color: #333;
    line-height: 1.2;
    padding: 0 25px 0 60px
}

input.input100 {
    height: 55px
}

textarea.input100 {
    min-height: 162px;
    padding-top: 22px;
    padding-bottom: 15px
}

.focus-input100 {
    position: absolute;
    display: block;
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    top: -1px;
    left: -1px;
    pointer-events: none;
    border: 1px solid;
    border-radius: 2px;
    border-color: #846add;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s;
    -webkit-transform: scaleX(1.1) scaleY(1.3);
    -moz-transform: scaleX(1.1) scaleY(1.3);
    -ms-transform: scaleX(1.1) scaleY(1.3);
    -o-transform: scaleX(1.1) scaleY(1.3);
    transform: scaleX(1.1) scaleY(1.3)
}

.input100:focus+.focus-input100 {
    visibility: visible;
    opacity: 1;
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1)
}

.input100:focus+.focus-input100+.label-input100 {
    color: #846add
}

.container-contact100-form-btn {
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 17px
}

.contact100-form-btn {
    font-family: Montserrat-Bold;
    font-size: 12px;
    color: #fff;
    line-height: 1.2;
    text-transform: uppercase;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    min-width: 160px;
    height: 42px;
    border-radius: 21px;
    background: #846add;
    box-shadow: 0 10px 30px 0 rgba(132, 106, 221, .5);
    -moz-box-shadow: 0 10px 30px 0 rgba(132, 106, 221, .5);
    -webkit-box-shadow: 0 10px 30px 0 rgba(132, 106, 221, .5);
    -o-box-shadow: 0 10px 30px 0 rgba(132, 106, 221, .5);
    -ms-box-shadow: 0 10px 30px 0 rgba(132, 106, 221, .5);
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s
}

.contact100-form-btn:hover {
    background: #333;
    box-shadow: 0 10px 30px 0 rgba(51, 51, 51, .5);
    -moz-box-shadow: 0 10px 30px 0 rgba(51, 51, 51, .5);
    -webkit-box-shadow: 0 10px 30px 0 rgba(51, 51, 51, .5);
    -o-box-shadow: 0 10px 30px 0 rgba(51, 51, 51, .5);
    -ms-box-shadow: 0 10px 30px 0 rgba(51, 51, 51, .5)
}

@media(max-width:576px) {
    .contact100-form {
        padding: 40px 15px 48px
    }

    .input100 {
        padding: 0 25px 0 40px
    }

    .label-input100 {
        width: 40px
    }
}

.validate-input {
    position: relative
}

.alert-validate .focus-input100 {
    box-shadow: 0 5px 20px 0 rgba(250, 66, 81, .1);
    -moz-box-shadow: 0 5px 20px 0 rgba(250, 66, 81, .1);
    -webkit-box-shadow: 0 5px 20px 0 rgba(250, 66, 81, .1);
    -o-box-shadow: 0 5px 20px 0 rgba(250, 66, 81, .1);
    -ms-box-shadow: 0 5px 20px 0 rgba(250, 66, 81, .1)
}

.alert-validate::before {
    content: "";
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    position: absolute;
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    background-color: transparent;
    border: 1px solid #fa4251;
    border-radius: 2px;
    top: -1px;
    left: -1px;
    pointer-events: none
}

.btn-hide-validate {
    font-family: Material-Design-Iconic-Font;
    font-size: 18px;
    color: #fa4251;
    cursor: pointer;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 100%;
    top: 0;
    right: -25px
}

.rs1-alert-validate.alert-validate::before {
    background-color: #fff
}

.true-validate::after {
    content: "\f26b";
    font-family: Material-Design-Iconic-Font;
    font-size: 18px;
    color: #00ad5f;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 100%;
    top: 0;
    right: -25px
}

@media(max-width:576px) {
    .btn-hide-validate {
        right: 10px
    }

    .true-validate::after {
        right: 10px
    }
}

